*,
*::before,
*::after {
  box-sizing: border-box;
}

::selection {
  color: $color-primary;

  background-color: $color-black;
}

:root {
  scroll-behavior: smooth;
}

html {
  min-width: 100vh;
  min-height: 100vh;

  font-size: 62.5%;

  overflow-x: hidden;
}

body {
  width: 100%;

  padding: 0;
  margin: 0;

  font-family: $ff-primary;
  font-size: $fs-regular;
  color: $color-white;

  background-color: $color-grey;

  overflow-x: hidden;

  @include respond(desktop) {
    font-size: 1.8rem;
  }
}

a,
a:link,
a:visited {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

p {
  margin: 0;
  padding: 0;
}
